import { clsx } from 'clsx';
import { CardProps } from './card.interface';

export const Card: React.FC<CardProps> = ({ children, image, isFluid = false, ...props }) => {
  const padding = image ? 'zep-pt-0 zep-px-0' : 'zep-px-1.5 zep-py-2';

  return (
    <div
      className={clsx(
        padding,
        'zep-outline-none',
        'zep-flex',
        'zep-flex-col',
        'zep-flex-nowrap',
        'zep-items-start',
        'zep-border-1',
        'zep-border-solid',
        'zep-border-greyscale-400',
        'hover:zep-border-greyscale-900',
        'zep-bg-background-light',
        'zep-rounded-8',
        '[&>img]:zep-rounded-tl-8',
        '[&>img]:zep-rounded-tr-8',
        { isFluid: 'zep-@container' },
      )}
      tabIndex={0}
      data-testid="zep-card"
      {...props}
    >
      {image}
      {image ? (
        <div
          className={clsx(
            'zep-flex',
            'zep-h-full',
            'zep-flex-col',
            'zep-flex-nowrap',
            'zep-items-start',
            'zep-p-1.5',
            'md:zep-p-2.5',
            'lg:zep-p-3',
          )}
        >
          {children}
        </div>
      ) : (
        children
      )}
    </div>
  );
};
